import { CqsChunkStory, CqsClusterSummaryStory, CqsStory } from "./query.types"

const isClusterStory = (
  item: CqsStory | CqsClusterSummaryStory | undefined,
): item is CqsClusterSummaryStory => {
  if (!item) return false
  return "cluster" in item && item.cluster !== null && item.cluster !== undefined
}

const isClusterChunkStory = (item: CqsChunkStory): item is Required<CqsChunkStory> => {
  return "cluster" in item && item.cluster !== null && item.cluster !== undefined
}

const isChunkStory = (item: CqsStory | CqsChunkStory): item is CqsChunkStory => {
  return "chunks" in item
}

export { isClusterStory, isClusterChunkStory, isChunkStory }
